
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus, :active {
  outline: none;
}
a:focus, a:active {
  outline: none;
}
nav, footer, header, aside {
  display: block;
}
html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  min-width: 320px;
  line-height: 1;
  font-size: 14px;
  font-family: "Lato";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input, button, textarea {
  font-family: "Lato";
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a, a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}
#app {
  max-width: 800px;
  padding: 100px 50px;
  margin: 0 auto;
}

* {
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
}
.title {
  font-size: 24px;
  margin: 0 0 30px 0;
}
.subtitle {
  position: relative;
  font-weight: 700;
  margin: 0 0 20px 0;
}
.subtitle::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px;
    height: 1px;
    width: 100%;
    background: #000;
}
.form__subtitle {
  margin: 0 0 10px 0;
}
.form__section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 100%;
}
.form__block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 200px;
  margin: 0 30px 30px 0;
}
.form__radio-block {
  margin: 0 30px 30px 0;
}
.form__select-block {
  width: 200px;
  margin: 0 30px 30px 0;
}
.form__submit {
  width: 150px;
  height: 40px;
  background: teal;
  border-radius: 25px;
  font-size: 15px;
}
.form__submit:hover {
    background: #4fe7e7;
}
.input {
  border: 1px solid #000;
  height: 30px;
  width: 200px;
  border-radius: 15px;
  padding: 10px;
}
.input:focus {
    background: rgba(115, 214, 115, 0.601);
}
.input:focus::-webkit-input-placeholder {
    opacity: 0;
}
.input:focus::-moz-placeholder {
    opacity: 0;
}
.input:focus::-ms-input-placeholder {
    opacity: 0;
}
.input:focus::placeholder {
    opacity: 0;
}
label {
  margin: 0 0 10px 0;
}
.title-sex {
  margin: 0 0 8px 0;
}
select {
  border: 1px solid rgba(0, 0, 0, 0.329);
}
.typed {
  background: #76f376;
}
.err {
  background: rgba(255, 0, 0, 0.496);
  -webkit-box-shadow: 0 0 10px #db6f6f;
          box-shadow: 0 0 10px #db6f6f;
}
.error-block {
  visibility: hidden;
  opacity: 0;
  width: 220px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  background: white;
  border: 1px solid #000;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 3;
  -webkit-box-shadow: 0 0 5px #000;
          box-shadow: 0 0 5px #000;
  color: red;
  font-size: 14px;
}
.error-block.active {
  visibility: visible;
  opacity: 1;
}
.ok-block {
  visibility: hidden;
  opacity: 0;
  width: 220px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  background: white;
  border: 1px solid #000;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 3;
  -webkit-box-shadow: 0 0 5px #000;
          box-shadow: 0 0 5px #000;
  color: #00ff26;
  font-size: 14px;
}
.ok-block.active {
  visibility: visible;
  opacity: 1;
}
